import axios from 'axios';
import * as constants from './constants';
import { Modal, message } from 'antd';
import backTop from "../../../../utils";

// 群发公号
const getData = (page = 1, channel = "", creator = "") => {
  return new Promise((resolve, reject) => {
    axios.get("/tuiguang/novel/show",{params: {page, channel, creator}})
      .then(res => {
        const { data: novelLinkList, domain_names, count } = res.data;
        const action = {
          type: constants.GET_NOVEL_LINK_LIST,
          novelLinkList, domain_names, count
        };
        resolve(action)
      })
  });
};

export const getNovelLinkList = creator => {
  return async dispatch => {
    let action = await getData(1, "", creator);
    dispatch(changeStateInput("creator", creator));
    dispatch(action);
  }
};

const getDataByCondition = (page = 1, channel = "", creator = "") => {
  return new Promise((resolve, reject) => {
    axios.get("/tuiguang/novel/show",{params: {page, channel, creator}})
      .then(res => {
        const { data: novelLinkList, domain_names, count, code, msg } = res.data;
        const action = {
          type: constants.GET_NOVEL_LINK_LIST,
          novelLinkList, domain_names, count
        };
        message.info(msg);
        code === 1 && localStorage.setItem("creator", creator)
        resolve(action)
      })
  });
};

export const changePage = (page) => {
  return async dispatch => {
    dispatch(await getData(page));
    const action = {
      type: constants.CHANGE_PAGE,
      page
    };
    await backTop();
    await dispatch(action);
  }
};

export const deleteNovel = (id, index) => {
  return dispatch => {
    Modal.confirm({
      title: "确定删除这条链接吗？",
      onOk: () => {
        axios.post("/tuiguang/novel/delete_one", {id: id})
          .then(res => {
            if(res.data.message) {
              message.info(res.data.message);
              const action = {
                type: constants.DELETE_NOVEL,
                index
              };
              dispatch(action);
            }
          });
      }
    })
  }
};

export const goTop = id => {
  return dispatch => {
    axios.post('/tuiguang/goTop', {id})
      .then(res => {
        if(res.data.success) {
          message.success(res.data.success);
          dispatch(getNovelLinkList());
        }
      })
  }
};

export const cancelGoTop = id => {
  return dispatch => {
    axios.post('/tuiguang/cancelGoTop', {id})
      .then(res => {
        if(res.data.success) {
          message.success(res.data.success);
          dispatch(getNovelLinkList());
        }
      })
  }
}

export const getChannelList = () => {
  return dispatch => {
    axios.get('/channel')
      .then(res => {
        if(res.data.success) {
          const channelList = res.data.data;
          const action = {
            type: constants.GET_CHANNEL_LIST,
            channelList
          };
          dispatch(action);
        }
      })
  }
};

