import React, {Component} from 'react';
import BreadCrumb from '../../../common/bread';
import {Table, Modal, message, Form, Input} from 'antd';
import CopyToClipboard from 'react-copy-to-clipboard';

import axios from "axios";
import Button from '../../../common/components/Button';

class ZhuiShuYun extends Component {
  constructor(props) {
    super(props);
    this.state = {
      zsyData: [],
      domain_name: "",
      linkModal: false,
      linkInfo: {}
    }
  }

  getLinks() {
    axios.get("/zsy").then(res => {
      let {code, data, domain_name} = res.data;
      if(code === 1) {
        this.setState({
          zsyData: data,
          domain_name
        })
      } else {
        this.setState({
          zsyData: [],
          domain_name
        })
      }
    })
  }

  deleteLink(_id) {
    Modal.confirm({
      title: "确定删除这条内容吗？",
      onOk: () => {
        axios.delete("/zsy", {params: {_id}})
          .then(res => {
            let {code, msg} = res.data;
            if(code === 1) {
              this.getLinks()
              message.success(msg)
            } else {
              message.error(msg)
            }
          })
      }
    })
  }

  changeInputValue(value, name) {
    let {linkInfo} = this.state;
    linkInfo[name] = value;
    this.setState({linkInfo})
  }

  openUpdateModal(linkInfo) {
    this.setState({linkInfo, linkModal: true})
  }

  cancelAction() {
    this.setState({linkInfo: {}, linkModal: false})
  }

  submitLink() {
    let {linkInfo} = this.state;
    if(!linkInfo._id) {
      this.createLink(linkInfo)
    } else {
      this.updateLink(linkInfo)
    }
  }

  createLink(linkInfo) {
    Modal.confirm({
      title: "确定新建一条内容？",
      onOk: () => {
        axios.post("/zsy", linkInfo)
          .then(res => {
            let {code, msg} = res.data;
            if(code === 1) {
              this.cancelAction();
              this.getLinks();
              message.success(msg)
            } else {
              message.error(msg)
            }
          })
      }
    })
  }
  
  updateLink(linkInfo) {
    Modal.confirm({
      title: "确定修改这条内容？",
      onOk: () => {
        axios.put("/zsy", linkInfo)
          .then(res => {
            let {code, msg} = res.data;
            if(code === 1) {
              this.cancelAction();
              this.getLinks();
              message.success(msg)
            } else {
              message.error(msg)
            }
          })
      }
    })
  }

  componentDidMount() {
    this.getLinks()
  }

  render() {
    let {zsyData, domain_name, linkModal, linkInfo} = this.state;
    const zsyHeader = [
      {
        key: "gonghao_name",
        dataIndex: "gonghao_name",
        title: "公号名称",
        align: "center"
      },
      {
        key: "channel_id",
        dataIndex: "channel_id",
        title: "渠道id",
        align: "center"
      },
      {
        key: "tuiguang_link",
        dataIndex: "tuiguang_link",
        title: "推广链接",
        align: "center"
      },
      {
        key: "_id",
        dataIndex: "_id",
        title: "复制链接",
        align: "center",
        width: 100,
        render: (text, record) => <CopyToClipboard text={domain_name + "/zsy/" + record._id} onCopy={() => message.success("复制成功")}>
          <Button background="success" buttonContent="复制链接" />
        </CopyToClipboard>
      },
      {
        title: "Action",
        align: "center",
        width: 200,
        render: (text, record, index) => <div>
          <Button buttonContent="修改" onClick={() => this.openUpdateModal({...record})} />
          <Button background="danger" buttonContent="删除" onClick={() => this.deleteLink(record._id)} />
        </div>
      }
    ];
    return (
      <div>
        <BreadCrumb/>
        <div className="container">
          <Button size="default" background="success" mb={20} buttonContent="添加新链接" onClick={() => this.setState({linkModal: true})} />
          <Table rowKey={record => record._id} size="small" bordered columns={zsyHeader} dataSource={zsyData}/>
          <Modal visible={linkModal} title="编辑链接信息" onOk={() => this.submitLink()} onCancel={() => this.cancelAction()}>
            <Form>
              <Form.Item label="公号名称">
                <Input value={linkInfo.gonghao_name || ""} onChange={e => this.changeInputValue(e.target.value, "gonghao_name")} />
              </Form.Item>
              <Form.Item label="渠道id">
                <Input value={linkInfo.channel_id || ""} onChange={e => this.changeInputValue(e.target.value, "channel_id")} />
              </Form.Item>
              <Form.Item label="推广链接">
                <Input value={linkInfo.tuiguang_link || ""} onChange={e => this.changeInputValue(e.target.value, "tuiguang_link")} />
              </Form.Item>
            </Form>
          </Modal>
        </div>
      </div>
    )
  }
}

export default ZhuiShuYun;