import React, { Component } from 'react';
import BreadCrumb from '../../../common/bread';
import { Table, Modal, Radio, message, Tooltip, Select, Input, Divider, Icon } from 'antd';
import { connect } from "react-redux";
import { actionCreators } from './store';
import CopyToClipboard from 'react-copy-to-clipboard';
import { Link } from 'react-router-dom';
import Button from '../../../common/components/Button';

import "./index.scss";

const {Option} = Select;


class NovelLink extends Component {
  componentWillMount() {
    const creator = localStorage.getItem("creator");
    this.props.getNovelLinkList(creator || "全部");
    this.props.getChannelList();
    this.props.getCreatorList();
  }

  render() {
    const { novelLinkList, count, creator, domain, domainModal, channelModal, channel, channelList, selectedChannel, changePage, deleteNovel, goTop, cancelGoTop, copyNovel, selectChannel, cancelSelectChannel, openChannelModal, changeChannel, openDomainModal, closeDomainModal, submitDomain, changeDomainName, changeCreator, creatorList, addCreator, openAddCreatorModal, delCreator, openDelCreatorModal, addCreatorModal, closeAddCreatorModal, delCreatorModal, closeDelCreatorModal, newCreator, changeStateInput, needDelCreatorId } = this.props;
    const novelLinkHeader = [
      {
        key: "id",
        dataIndex: "id",
        title: "id",
        align: "center",
        render: text => <Tooltip title={text.length > 10 ? text : ""}><p className="textEllipsis" style={{width: 80}}>{text}</p></Tooltip>
      },
      {
        title: "类型",
        align: "center",
        render: (text, record) => record.type === 0 ? "微跳" : record.type === 1 ? "非微单" : record.type === 2 ? "非微多" : "头条"
      },
      {
        key: "name",
        dataIndex: "name",
        title: "公号",
        align: "center"
      },
      {
        key: "domain_name",
        dataIndex: "domain_name",
        title: "域名",
        align: "center",
        render: text => <Tooltip title={text}><p className="textEllipsis" style={{width: 100}}>{text}</p></Tooltip>
      },
      {
        key: "channel",
        dataIndex: "channel",
        title: "渠道",
        align: "center",
        render: text => <Tooltip title={text || ""}><p className="textEllipsis" style={{width: 100}}>{text || "~~"}</p></Tooltip>
      },
      {
        key: "remarks",
        dataIndex: "remarks",
        title: "备注",
        align: "center",
        render: text => <Tooltip title={text || ""}><p className="textEllipsis" style={{width: 200}}>{text || "~~"}</p></Tooltip>
      },
      {
        title: "置顶",
        align: "center",
        render: (text, record) => <Button background="info" buttonContent={record.zIndex === 0 ? "置顶" : "取消置顶"} onClick={() => {
          record.zIndex === 0 ? goTop(record._id) : cancelGoTop(record._id)
        }}/>
      },
      {
        title: "复制链接",
        align: "center",
        render: (text, record) => <CopyToClipboard text={record.domain_name + "/tuiguang/" + (record.type === 0 ? "weitiao/" : record.type === 1 ? "singlepage/" : record.type === 2 ? "multipage/" : record.type === 3 ? "toutiao/" : "data/") + record.id} onCopy={() => message.success("复制成功")}>
          <Button background="success" buttonContent="复制链接" />
        </CopyToClipboard>
      },
      {
        title: "Action",
        align: "center",
        width: 200,
        render: (text, record, index) => <div>
          <Button background="update" buttonContent={<Link to={{pathname: "./update", query: record}}>修改</Link>} />
          <Button background="danger" buttonContent="删除" onClick={() => deleteNovel(record.id, index)} />
          <Button background="success" mr={0} buttonContent="复制一条" onClick={() => copyNovel(record, this.props)} />
        </div>
      }
    ];

    const pagination = {
      pageSize: 20,
      total: count,
      onChange: page => changePage(page),
    };
    const ChannelListWrapper = channelList.map(item => <Radio.Button style={{marginRight: 10, marginBottom: 10}} value={item.name} key={item._id}>{item.name}</Radio.Button>);
    return (
    	<div>
        <BreadCrumb />
        <div className="container">
          <div className="fr">
            <Button background="update" ml={20} size="default" buttonContent="修改域名"  onClick={openDomainModal}/>
            <Button background="success" size="default" buttonContent={<Link to="./create">新建小说链接</Link>} />
          </div>
          <Select
            value={creator}
            onChange={changeCreator}
            style={{width: 120, textAlign: "center"}}
            dropdownRender={menu => (
              <div>
                {menu}
                <Divider style={{ margin: '4px 0' }} />
                <div
                  style={{ padding: '8px', cursor: 'pointer' }}
                  onMouseDown={e => e.preventDefault()}
                  onClick={openAddCreatorModal}
                >
                  <Icon type="plus" /> 新增
                </div>
                <div style={{display: creatorList.length > 0 ? "block" : "none"}}>
                  <Divider style={{ margin: '4px 0' }} />
                  <div
                    style={{ padding: '8px', cursor: 'pointer' }}
                    onMouseDown={e => e.preventDefault()}
                    onClick={openDelCreatorModal}
                  >
                    <Icon type="close" /> 删除
                  </div>
                </div>
              </div>
            )}
          >
            <Option value="全部">全部</Option>
            {creatorList.map((item, index) => <Option value={item.name} key={item._id}>{item.name}</Option>)}
          </Select>
          <Button mb={20} ml={10} size="default" onClick={openChannelModal} buttonContent={selectedChannel} />
          <Table bordered size="small" rowKey={record => record._id} columns={novelLinkHeader} rowClassName={record => record.zIndex !== 0 ? "selectedRow" : ""} dataSource={novelLinkList} pagination={{...pagination}} />
        </div>
        <Modal
          title="请选择渠道"
          visible={channelModal}
          onOk={() => selectChannel(channel)}
          onCancel={cancelSelectChannel}
        >
          <Radio.Group value={channel} onChange={changeChannel}>
            <Radio.Button style={{marginRight: 10}} value="默认">默认</Radio.Button>
            {ChannelListWrapper}
          </Radio.Group>
        </Modal>
        <Modal
          title="请输入新域名"
          visible={domainModal}
          onOk={() => submitDomain(domain)}
          onCancel={closeDomainModal}
        >
          <Input placeholder="请输入新域名，如：https://www.baidu.com" value={domain} onChange={changeDomainName} />
        </Modal>
        <Modal
          title="新增创建人"
          visible={addCreatorModal}
          onOk={() => addCreator(newCreator)}
          onCancel={closeAddCreatorModal}
        >
          <Input placeholder="请输入新的创建人" value={newCreator} onChange={e => changeStateInput("newCreator", e.target.value)} />
        </Modal>
        <Modal
          title="请选择要删除的创建人"
          visible={delCreatorModal}
          onOk={() => delCreator(needDelCreatorId)}
          onCancel={closeDelCreatorModal}
        >
          <Radio.Group value={needDelCreatorId} onChange={e => changeStateInput("needDelCreatorId", e.target.value)}>
            {creatorList.map(item => <Radio key={item._id} value={item._id}>{item.name}</Radio>)}
          </Radio.Group>
        </Modal>
    	</div>
    )
  }

}

const mapState = state => ({
  novelLinkList: state.novel.links.novelLinkList,
  count: state.novel.links.count,
  channelModal: state.novel.links.channelModal,
  channel: state.novel.links.channel,
  channelList: state.novel.links.channelList,
  selectedChannel: state.novel.links.selectedChannel,
  domainModal: state.novel.links.domainModal,
  domain: state.novel.links.domain,
  creator: state.novel.links.creator,
  creatorList: state.novel.links.creatorList,
  addCreatorModal: state.novel.links.addCreatorModal,
  delCreatorModal: state.novel.links.delCreatorModal,
  newCreator: state.novel.links.newCreator,
  needDelCreatorId: state.novel.links.needDelCreatorId,
});

const mapDispatch = dispatch => ({
  changeCreator(value) {
    dispatch(actionCreators.searchByCreator(value))
  },
  getNovelLinkList(creator) {
    dispatch(actionCreators.getNovelLinkList(creator))
  },
  changePage(page) {
    dispatch(actionCreators.changePage(page))
  },
  deleteNovel(id, index) {
    dispatch(actionCreators.deleteNovel(id, index))
  },
  goTop(id) {
    dispatch(actionCreators.goTop(id))
  },
  cancelGoTop(id) {
    dispatch(actionCreators.cancelGoTop(id))
  },
  copyNovel(record, props) {
    dispatch(actionCreators.submitNovel(record, props));
  },
  getChannelList() {
    dispatch(actionCreators.getChannelList())
  },
  openChannelModal() {
    dispatch(actionCreators.openChannelModal())
  },
  cancelSelectChannel() {
    dispatch(actionCreators.cancelSelectChannel())
  },
  selectChannel(channel) {
    dispatch(actionCreators.selectChannel(channel))
  },
  changeChannel(e) {
    dispatch(actionCreators.changeChannel(e.target.value))
  },
  openDomainModal() {
    dispatch(actionCreators.openDomainModal())
  },
  closeDomainModal() {
    dispatch(actionCreators.closeDomainModal())
  },
  submitDomain(domain) {
    dispatch(actionCreators.submitDomain(domain))
  },
  changeDomainName(e) {
    dispatch(actionCreators.changeDomainName(e.target.value))
  },
  openAddCreatorModal() {
    dispatch(actionCreators.openAddCreatorModal())
  },
  closeAddCreatorModal() {
    dispatch(actionCreators.closeAddCreatorModal())
  },
  openDelCreatorModal() {
    dispatch(actionCreators.openDelCreatorModal())
  },
  closeDelCreatorModal() {
    dispatch(actionCreators.closeDelCreatorModal())
  },
  addCreator(newCreator) {
    dispatch(actionCreators.addCreator(newCreator))
  },
  delCreator(needDelCreatorId) {
    dispatch(actionCreators.delCreator(needDelCreatorId))
  },
  changeStateInput(key, value) {
    dispatch(actionCreators.changeStateInput(key, value))
  },
  getCreatorList() {
    dispatch(actionCreators.getCreatorList())
  }
});

export default connect(mapState, mapDispatch)(NovelLink);